import React, { Component } from 'react'
import { connect } from "react-redux";
import * as actions from "../actions";

class Home extends Component {
	componentDidMount() {
		this.props.demoAction();
	}
  render() {
		return (
			<div>
			Home changed
			</div>
		)
  }
}


export default connect(null, actions)(Home);


